import { StatusBar } from "expo-status-bar";
import {
  Button,
  Pressable,
  SafeAreaView,
  ScrollView,
  StyleSheet,
  Text,
  View,
} from "react-native";
import { useState, useCallback, useRef, useMemo } from "react";

export default function App() {
  const [nonce, setNonce] = useState(0);
  const resetSheet = useCallback(() => setNonce(nonce + 1), [nonce, setNonce]);

  return <SampleSheet key={nonce} reset={resetSheet} />;
}

function SampleSheet(props: { reset: () => void }) {
  const [total, setTotal] = useState(30);
  const [isComplete, setIsComplete] = useState(false);

  const categoryValuesRef = useRef<Record<string, number>>({
    Uniformity: 10,
    "Clean Cup": 10,
    Sweetness: 10,
    Defects: 0,
  });
  const setValueForCategory = useCallback(
    (name: string, value: number) => {
      const curVal = categoryValuesRef.current[name];
      if (value === curVal) {
        return;
      }
      categoryValuesRef.current[name] = value;
      const newTotal = Object.values(categoryValuesRef.current).reduce(
        (curr, next) => (curr || 0) + (next || 0)
      );
      setTotal(newTotal);
      setIsComplete(
        Object.values(categoryValuesRef.current).filter((val) => !isNaN(val))
          .length === 11
      );
    },
    [setTotal, categoryValuesRef]
  );
  const motd = useMemo(() => {
    const messages = [
      "üòé Don't Overthink It üèùÔ∏è",
      "ü§ò Good Cuppers Are Confident üé∏",
      "‚òïÔ∏è This Should Be Fun ‚ù§Ô∏è",
    ];
    return messages[Math.floor(Math.random() * messages.length)];
  }, []);

  return (
    <SafeAreaView style={styles.container}>
      <StatusBar style="auto" />
      <ScrollView style={{ flexDirection: "column" }}>
        <InfoSection motd={motd} />
        <Category
          name="Fragrance/Aroma"
          setValueForCategory={setValueForCategory}
          categoryValues={categoryValuesRef.current}
          scoringType="quality"
        />
        <Category
          name="Flavor"
          setValueForCategory={setValueForCategory}
          categoryValues={categoryValuesRef.current}
          scoringType="quality"
        />
        <Category
          name="Aftertaste"
          setValueForCategory={setValueForCategory}
          categoryValues={categoryValuesRef.current}
          scoringType="quality"
        />
        <Category
          name="Acidity"
          setValueForCategory={setValueForCategory}
          categoryValues={categoryValuesRef.current}
          scoringType="quality"
        />
        <Category
          name="Body"
          setValueForCategory={setValueForCategory}
          categoryValues={categoryValuesRef.current}
          scoringType="quality"
        />
        <Category
          name="Uniformity"
          setValueForCategory={setValueForCategory}
          categoryValues={categoryValuesRef.current}
          scoringType="technical"
        />
        <Category
          name="Balance"
          setValueForCategory={setValueForCategory}
          categoryValues={categoryValuesRef.current}
          scoringType="quality"
        />
        <Category
          name="Clean Cup"
          setValueForCategory={setValueForCategory}
          categoryValues={categoryValuesRef.current}
          scoringType="technical"
        />
        <Category
          name="Sweetness"
          setValueForCategory={setValueForCategory}
          categoryValues={categoryValuesRef.current}
          scoringType="technical"
        />
        <Category
          name="Overall"
          setValueForCategory={setValueForCategory}
          categoryValues={categoryValuesRef.current}
          scoringType="quality"
        />
        <Category
          name="Defects"
          setValueForCategory={setValueForCategory}
          categoryValues={categoryValuesRef.current}
          scoringType="defects"
        />
        <View style={{ height: 200 }} />
      </ScrollView>
      <Header
        total={total}
        isComplete={isComplete}
        reset={props.reset}
        categoryValues={categoryValuesRef.current}
      />
    </SafeAreaView>
  );
}

const styles = StyleSheet.create({
  container: {
    backgroundColor: "#fff",
    alignItems: "stretch",
    justifyContent: "flex-start",
    position: "absolute",
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
  },
});

